@tailwind base;
@tailwind components;
@tailwind utilities;

.header {
  display: flex;
  justify-content: space-between;
  background-color: #ffffff; /* Light background */
  color: #000000; /* Dark text */
  align-items: center;
  padding: 10px 15px; /* Increased padding for a more spacious look */
  height: 90px; /* Adjusted height for a more prominent header */
  font-family: "Poppins", sans-serif; /* Font similar to Swiggy */
}

.logo {
  width: 100px; /* Increased logo size */
  cursor: pointer;
}

/* Navigation Items */
.nav-items > ul {
  display: flex;
  font-size: 20px; /* Adjusted font size */
  list-style: none;
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}

.nav-items > ul > li {
  padding: 0 15px; /* Adjusted padding */
  margin: 0;
}

.nav-items {
  padding: 0;
}

/* Body Styling */
.body {
  border-radius: 8px;
  background-color: #f5f5f5;
  padding: 20px;
  min-height: calc(100vh - 80px); /* Adjusted for new header height */
  font-family: "Poppins", sans-serif;
}

/* Restaurant Card Styling */
.res-card {
  width: 250px;
  padding: 20px;
  background: linear-gradient(145deg, #fff, #f9f9f9);
  border-radius: 16px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  overflow: hidden;
  margin: 15px;
  text-align: center;
  height: 400px;

  position: relative;
}

.res-card:hover {
  transform: translateY(-10px);
  cursor: pointer;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

/* Uniform Card Image Styling */
.res-logo {
  width: 100%;
  height: 160px;
  object-fit: cover;
  border-radius: 12px;
  margin-bottom: 15px;
  transition: transform 0.3s ease;
}

.res-card:hover .res-logo {
  transform: scale(1.05);
}

/* Restaurant Name Styling */
.res-card h3 {
  font-size: 18px;
  margin: 12px 0;
  color: #333;
  font-weight: 700;
  text-transform: capitalize;
  letter-spacing: 0.5px;
  white-space: nowrap; /* Prevent wrapping */
  overflow: hidden; /* Hide overflow */
  text-overflow: ellipsis; /* Add ellipsis */
}

/* Restaurant Info */
.res-card h4 {
  font-size: 15px;
  margin: 5px 0;
  color: #888;
  font-weight: 400;
}

/* Cuisine Tags Styling */
.cuisine-tags {
  display: flex;
  justify-content: center;
  gap: 5px;
  margin-top: 10px;
}

.cuisine-tag {
  background-color: #ffe8d6;
  color: #d17b33;
  font-size: 12px;
  padding: 5px 8px;
  border-radius: 8px;
  text-transform: uppercase;
}

/* Delivery Time and Price */
.delivery-price-info {
  font-size: 14px;
  color: #555;
  margin-top: 10px;
}

/* Button-like Elements */
.res-card button {
  background-color: #ff8c00;
  color: #fff;
  padding: 10px 16px;
  border: none;
  border-radius: 8px;
  margin-top: 12px;
  transition: background-color 0.3s ease;
}

.res-card button:hover {
  background-color: #e07b00;
  cursor: pointer;
}

/* Container Styling */
.res-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 40px;
  background-color: #fafafa;
}

/* Responsive Design */
@media (max-width: 768px) {
  .res-card {
    width: 180px;
    padding: 15px;
  }

  .res-card h3 {
    font-size: 16px;
  }

  .res-card h4 {
    font-size: 13px;
  }

  .res-logo {
    height: 140px;
  }
}

/* Filter Button */
.filter-btn {
  background: linear-gradient(135deg, #007bff 0%, #0056d2 100%);
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 25px; /* Rounded corners */
  cursor: pointer;
  font-size: 16px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease, transform 0.3s ease;
  font-family: "Poppins", sans-serif;
  margin-right: 10px;
}

.filter-btn:hover {
  background: linear-gradient(135deg, #0056d2 0%, #003b9b 100%);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15);
  transform: translateY(-2px);
}

/* Shimmer Wrapper */
.shimmer-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  max-width: 100%;
}

/* Shimmer Card */
.shimmer-card {
  width: 220px;
  height: 340px;
  background-color: #e0e0e0;
  border-radius: 12px;
  position: relative;
  overflow: hidden;
}

/* Shimmer Effect */
.shimmer-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: -150%;
  width: 150%;
  height: 100%;
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.6) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  animation: shimmer 2s infinite;
}

/* Shimmer Animation */
@keyframes shimmer {
  0% {
    left: -150%;
  }
  50% {
    left: 100%;
  }
  100% {
    left: 150%;
  }
}

