/* Navigation Icon Styling */
.nav-icon {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
  font-family: "Poppins", sans-serif;
}

.nav-icon li {
  position: relative;
}

.nav-icon a {
  text-decoration: none; /* Removes the blue underline */
  color: #333; /* Adjust color as needed */
  font-size: 18px; /* Adjust font size */
  font-weight: 600; /* Makes the text bold */
  padding: 10px 15px; /* Adds padding to make it look like a button */
  border-radius: 25px; /* Rounds the corners for a button-like appearance */
  transition: background-color 0.3s ease, color 0.3s ease;
}

.nav-icon a:hover {
  background-color: #f0f0f0; /* Background color on hover */
  color: #007bff; /* Text color on hover */
}

/* ContactPage.css */
.contact-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #f8f8f8;
  margin: 0; /* Remove any default margin */
  padding: 20px; /* Add padding to ensure the content doesn't touch the edges */
  box-sizing: border-box; /* Ensure padding doesn't affect the size of elements */
}

.contact-card {
  padding: 20px;
  border-radius: 12px;
  background-color: #ffffff;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
  text-align: center;
  max-width: 450px;
  width: 100%;
  border-top: 8px solid #ff7e00; /* Swiggy-like orange accent */
}

.contact {
  margin: 0 0 25px 0;
  font-size: 2.5rem;
  color: #ff7e00; /* Swiggy-like orange */
}

.contact-number,
.contact-email {
  margin: 15px 0;
  font-size: 1.3rem;
  color: #333;
}

.contact-number strong,
.contact-email strong {
  color: #ff7e00; /* Swiggy-like orange */
}

/* Adding hover effect for card */
.contact-card:hover {
  box-shadow: 0px 6px 30px rgba(0, 0, 0, 0.15);
}

/* Responsive design */
@media (max-width: 600px) {
  .contact-card {
    padding: 15px;
    max-width: 90%;
  }

  .contact {
    font-size: 2rem;
  }

  .contact-number,
  .contact-email {
    font-size: 1.1rem;
  }
}
